import React, { useState } from 'react';
import styled from 'styled-components';
import Responsive from '../common/Responsive';
import Button from '../common/Button';
import palette from '../../lib/styles/palette';
import Criterias from '../common/Criterias';
import { Link } from 'react-router-dom';
import CriteriaReadActionButtons from '../criteria/CriteriaReadActionButtons';
import { removeCriteria } from '../../lib/api/criteria';

const CriteriaListBlock = styled(Responsive)`
  margin-top: 1rem;
`;

const WriteCriteriaButtonWrapper = styled.div`
  margin-top: 3rem;
  margin-bottom: 3rem;
`;

const CriteriaItemBlock = styled.div`
  padding-top: 3rem;
  padding-bottom: 3rem;
  /* border-top: 1px solid ${palette.gray[2]}; */
  /*   item  padding-top  */
  &:first-child {
    padding-top: 0;
  }
  /* & + & {
    border-top: 1px solid ${palette.gray[2]};
  } */
  h2 {
    font-size: 2rem;
    margin-bottom: 0;
    margin-top: 0;
  }
  p {
    margin-top: 2rem;
  }
  &:hover {
    background: ${palette.gray[1]};
  }
`;

const CriteriaItem = ({
  criteria,
  onReload,
  onTempChangeField,
  showWriteButton,
}) => {
  const { question, details, _id } = criteria;
  const onRemove = async () => {
    try {
      await removeCriteria(_id);
      onReload();
    } catch (e) {
      console.log(e);
    }
  };
  const onTempClick = () => {
    /**
     *  todo :  read    (json     redux    -_-;)
     *  h2   criteria  store  criteria  set
     * */
    onTempChangeField({ criteria });
  };
  return (
    <>
      <Link to={`/readCriteria/${_id}`}>
        <CriteriaItemBlock onClick={onTempClick}>
          <h2>{question.no}</h2>

          <Criterias criterias={details} />
        </CriteriaItemBlock>
      </Link>
      {showWriteButton && <CriteriaReadActionButtons onRemove={onRemove} />}
    </>
  );
};

const CriteriaList = ({
  testinfoId,
  criterias,
  loading,
  error,
  showWriteButton,
  onReload,
  onTempChangeField,
}) => {
  //  
  if (error) {
    return <CriteriaListBlock> .</CriteriaListBlock>;
  }

  const writeLink = `/writeCriteria/${testinfoId}`;
  return (
    <>
    
      <CriteriaListBlock>
        <WriteCriteriaButtonWrapper>
          {showWriteButton && (
            <Button cyan big to={writeLink}>
                 
            </Button>
          )}
        </WriteCriteriaButtonWrapper>  
        {/*       */}
        {!loading && criterias && (
          <>
            <div>
              {criterias.map(criteria => (
                <CriteriaItem
                  criteria={criteria}
                  key={criteria._id}
                  onReload={onReload}
                  onTempChangeField={onTempChangeField}
                  showWriteButton={showWriteButton}
                />
              ))}
            </div>
          </>
        )}
      </CriteriaListBlock>
    </>
  );
};

export default CriteriaList;
