import React, { useState } from 'react';
import styled from 'styled-components';
import Button from '../common/Button';
import AskModal from '../common/AskModal';

const CriteriaActionButtonsBlock = styled.div`
  margin-top: 1rem;
  margin-bottom: 3rem;
  button + button {
    margin-left: 0.5rem;
  }
`;

//         
const StyledButton = styled(Button)`
  height: 2.215rem;
  & + & {
    margin-left: 0.5rem;
  }
`;

const CriteriaActionButtons = ({ user, criteriaId, onCancel, onPublish }) => {
  const title = criteriaId ? ' ' : ' ';
  const desc = title + ' .';

  const [modal, setModal] = useState(false);
  const onConfirm = () => {
    setModal(false);
  };
  const [errorModal, setErrorModal] = useState(false);
  const onErrorConfirm = () =>{
    setErrorModal(false)
  }

  const onPublishClick = () => {
    // todo :      ?
    

    setModal(true);
    onPublish();
  };

  return (
    <>
      <CriteriaActionButtonsBlock>
        {user && (
          <StyledButton cyan onClick={onPublishClick}>
            {title}
          </StyledButton>
        )}

        <StyledButton onClick={onCancel}></StyledButton>
      </CriteriaActionButtonsBlock>
      <AskModal
        visible={modal}
        title={title}
        description={desc}
        onConfirm={onConfirm}
        showCancel={false}
      />
      <AskModal 
        visible={errorModal}
        title={""}
        
      />
    </>
  );
};

export default CriteriaActionButtons;
